<style lang="less" scoped>
  // 头部
  .header {
    height: 90px;
    background-color: #fff;
    &>span{
      display: inline-block;
      
      &:nth-child(1){
        width: 20%;
        position: relative;
        &>img{
          position: absolute;
          top: 50%;
          left: 20px;
          transform: translateY(-70%);
          width: 30px;
          height: 50px;
        }
      }
      &:nth-child(2){
        width: 55%;
        text-align: center;
        &>input{
          font-size: 30px;
          outline: none;
          height: 70px;
          text-align: center;
          border: none;
          font-size: 30px;
          color: #888;
          width: 300px;
        }
        &>input::-webkit-input-placeholder{
          font-size: 30px;
        }
      }
      &:nth-child(3){
        width: 20%;
        text-align: right;
        color: #04be02;
        font-size: 30px;
      }
    }
  } // 头部
  .modal {
    width: 100%;
    background-color: #fff;
    &>div:nth-child(1) {
      height: 70px;
      width: 100%;
      text-align: center;
      font-size: 40px;
    }
  }
  .vip_course_item {
    height: 70px;
    width: 100%;
    text-align: center;
    img {
      width: 100px;
    }
  }
  .course_header_img_vip {
    overflow: hidden;
  }
  /* 课程列表 START*/
  .course_flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  .flex_middle {
    width: 46%;
    margin-top: 20px;
    background-color: #fff;
    padding-bottom: 20px;
  }
  .flex_middle:nth-child(odd){
    margin-left: 20px;
  }
  .flex_middle:nth-child(even){
    margin-right: 20px;
  }
  .flex_middle>div:nth-child(2) {
    padding-left: 20px;
  }
  .course_list .course_flex>div>.course_header_img>img {
    width: 100%;
  }
  .course_text_detail {
    display: -webkit-flex;
    display: flex;
    margin-top: 5px;
  }
  .course_text_detail>div>p {
    font-size: 30px;
    color: #777;
    white-space: nowrap;
    overflow: hidden;
    width: 200px;
    text-overflow: ellipsis;
    &:nth-child(2){
      &>img{
        width: 35px;
        margin-right: 10px;
      }
    }
  }
  .course_text_detail>div:nth-child(1) {
    -webkit-box-flex: 7;
    flex: 7;
    padding-left: 20px;
  }
  .course_text_detail>div:nth-child(2) {
    -webkit-box-flex: 3;
    flex: 3;
    &>img{
      width: 40px;
    }
  }
  .course_text_detail>div:nth-child(2)>button {
    background-color: transparent;
    border: .0625rem solid #555;
    border-radius: .625rem;
    width: 3.125rem;
    height: 1.875rem;
    color: #555;
  }
  .course_list {
    margin-bottom: 5.625rem;
  }
  /* 课程列表 END */
  /* 修改首页下拉推荐试学课列表显示效果 */
  .course_header_img {
    border-radius: 20px 20px 0 0;
    overflow: hidden;
    img {
      width: 100%;
      height: 220px;
    }
  }
  /* 修改首页下拉推荐试学课列表显示效果 */

  .title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 30px;
  }
  .collections {
    width: 50px;
    height: auto;
  }
</style>

<template>
  <div>
    <div class="header">
      <span @click="back"><img :src="back_img" alt="返回"></span>
      <!--  @input="searchWorks" -->
      <span><input type="text" name="searchWorks" class="searchWorks" id="search" placeholder="搜索课程名称" ref="searchWork"></span>
      <span @click="cancel">取消</span>
    </div>
    <div class="modal">
      <div v-if="showList">试学课</div>
      <div class="course_flex" v-if="showList">
        <!-- 第一个 -->
        <div class="flex_middle" v-for="(course ,index) in courses_list" @click.stop="__enterCourse(course)" :key="index">
          <div class="course_header_img"><img :src="course.coverImage ? course.coverImage : defaultCoverImg" :alt="course.keywords" :title="course.keywords"></div>
          <div>
            <div class="title">{{course.title}}</div>
          </div>
          <div class="course_text_detail">
            <div>
              <p>{{course.liveStatus == "LIVING"?"正在直播":"暂无直播"}}</p>
              <p class="course_text_detail_pad"><img src="../../assets/img/people1.png" class="course_text_detail_pad_img">{{course.totalStudent | numFm}}</p>
            </div>
            <div class="coll">
              <img class="collection collections" :src="course.isFollowed == 'Y' ? flowed : collection" @click.stop="favorite(course)">
            </div>
          </div>
        </div>
        <!-- 第一个 -->
      </div>
      <!-- VIP课程 -->
      <div v-if="showList" class="vip_course_item" style="margin-top:30px;"><img :src="vipImg" alt="VIP课程"></div>
      <div class="course_flex" v-if="showList">
        <!-- 第一个 -->
        <div class="flex_middle" v-for="(course ,index) in vip_list" @click="__enterVipCourse(course)" :key="index">
          <div class="course_header_img course_header_img_vip"><img :src="course.coverImage" :alt="course.keywords" :title="course.keywords"></div>
          <div>
            <div class="title">{{course.title}}</div>
          </div>
          <div class="course_text_detail">
            <div>
              <p>讲师：{{course.teacherNicknames[0]}}</p>
              <p class="course_text_detail_pad"><img src="../../assets/img/people1.png" class="course_text_detail_pad_img">{{course.totalStudent | numFm}}</p>
            </div>
            <div class="coll">
              <img class="collection collections" :src="course.isFollowed == 'Y' ? flowed : collection" @click.stop="favorite(course)">
            </div>
          </div>
        </div>
        <!-- 第一个 -->
      </div>
    </div>
    <div class="course_flex" v-if="isSearch">
      <!-- 第一个 -->
      <div class="flex_middle" v-for="(course, index) in searchList" v-if="course.courseType != 'VIP'" @click="__enterCourse(course)" :key="index">
        <div class="course_header_img course_header_img_vip"><img :src="course.coverImage" :alt="course.keywords" :title="course.keywords"></div>
        <div>
          <div class="title">{{course.title}}</div>
        </div>
        <div class="course_text_detail">
          <div>
            <p>讲师：{{course.teacherNicknames[0]}}</p>
            <p class="course_text_detail_pad"><img src="../../assets/img/people1.png" class="course_text_detail_pad_img">{{course.totalStudent | numFm}}</p>
          </div>
          <div class="coll">
            <img class="collection collections" :src="course.isFollowed == 'Y' ? flowed : collection" @click.stop="favorite(course)">
          </div>
        </div>
      </div>
      <!-- 第一个 -->
      <div class="flex_middle" v-for="(course, index) in searchList" v-if="course.courseType == 'VIP'" @click="__enterVipCourse(course)" :key="index">
        <div class="course_header_img course_header_img_vip"><img :src="course.coverImage" :alt="course.keywords" :title="course.keywords"></div>
        <div>
          <div class="title">{{course.title}}</div>
        </div>
        <div class="course_text_detail">
          <div>
            <p>讲师：{{course.teacherNicknames[0]}}</p>
            <p class="course_text_detail_pad"><img src="../../assets/img/people1.png" class="course_text_detail_pad_img">{{course.totalStudent | numFm}}</p>
          </div>
          <div class="coll">
            <img class="collection collections" :src="course.isFollowed == 'Y' ? flowed : collection" @click.stop="favorite(course)">
          </div>
        </div>
      </div>
      <!-- 第一个 -->
    </div>
    <login v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
  </div>
</template>
<script>
  import login from '@/components/login'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('homeModule')
  export default {
    components: {
      login
    },
    data() {
      return {
        courses_list: [],
        vip_list: [],
        collection: require('../../assets/img/heart_g.png'),
        flowed: require('../../assets/img/heart_r.png'),
        back_img: require('../../assets/img/per_fh.png'),
        showList: true,
        searchList: [],
        isSearch: false,
        showLoginModal: 0,
        page: 1,
        vipImg: require('../../assets/img/VIP.png'),
        falgNoData: false,
        pagination: true,
        showData: false,
        defaultCoverImg: require('../../assets/img/course-large.png')
      }
    },
    computed: {
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }
    },
    watch: {
      LOGIN_USER_OBJECT(state){
        if(state){
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          //add-获取课程数据
          this.GET_SEARCH_COURSES().then(data => {
            if (data.data == '没有更多课程了') {
              this.courses_list = []
              return
            } else {
              this.courses_list = data.data;
              this.courses_list.forEach((element) => {
                if (element && element.coverImage && element.coverImage.indexOf('http') == -1) {
                  element.coverImage = this.productURL + element.coverImage;
                }
              })

              window.onscroll = () => {
                this.dropDown()
              }
            }
          }).catch(err => {
            console.log(err)
          })

          //add-获取vip数据
          this.GET_SEARCH_VIP({
            page: this.page
          }).then(data => {
            if (data.data == '没有更多课程了') {
              this.vip_list = []
            } else {
              this.vip_list = data.data
              this.vip_list.forEach((element) => {
                if (element.coverImage && element.coverImage.indexOf('http') == -1) {
                  element.coverImage = this.productURL + element.coverImage
                }
              })
            }
            this.$Indicator.close()
          }).catch(err =>{
            console.log(err)
          })
        }
      }
    },
    mounted() {

      function debounce(handler, delay) {
          var timer;
          return function () {
              var self = this, arg = arguments;
              clearTimeout(timer);
              timer = setTimeout(function () {
                  handler.apply(self, arg)
              }, delay)
          }
      }

      // 监听输入, 函数完成后400ms执行
      this.addEvent(this.$refs.searchWork, 'input', debounce(this.searchWorks, 400))

      
      this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
      })
      //add-获取课程数据
      this.GET_SEARCH_COURSES().then(data => {
        if (data.data == '没有更多课程了') {
          this.courses_list = []
          return
        } else {
          this.courses_list = data.data;
          this.courses_list.forEach((element) => {
            if (element && element.coverImage && element.coverImage.indexOf('http') == -1) {
              element.coverImage = this.productURL + element.coverImage;
            }
          })
          window.onscroll = () => {
            this.dropDown()
          }
        }
      }).catch(err => {
        console.log(err)
      })

      //add-获取vip数据
      this.GET_SEARCH_VIP({
        page: this.page
      }).then(data => {
        if (data.data == '没有更多课程了') {
          this.vip_list = []
        } else {
          this.vip_list = data.data
          this.vip_list.forEach((element) => {
            if (element.coverImage && element.coverImage.indexOf('http') == -1) {
              element.coverImage = this.productURL + element.coverImage
            }
          })
        }
        this.$Indicator.close()
      }).catch(err =>{
        console.log(err)
      })
    },
    methods: {
      //调用store中的action
      ...mapActions([
        'GET_SEARCH_COURSES',
        'GET_SEARCH_VIP',
        'COURSES_ADD',
        'COURSES_DELETE',
        'SEARCH_COURSES'
      ]),
      dropDown() {
        //真实内容的高度
        var pageHeight = Math.max(document.body.scrollHeight, document.body.offsetHeight);
        //视窗的高度
        var viewportHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0;
        //隐藏的高度
        var scrollHeight = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
        if (this.falgNoData) { //数据全部加载完了
          return;
        } else if (pageHeight - viewportHeight - scrollHeight == 0 && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
              text: '加载中...',
              spinnerType: 'fading-circle'
          })
          this.page += 1;
          this.falgNoData = true; 
          this.GET_SEARCH_VIP({
            page: this.page
          }).then(data => {
            if (this.pagination) {
              if (data.data !== '没有更多课程了') {
                this.vip_list = this.vip_list.concat(data.data)
                this.$Indicator.close()
                this.falgNoData = false
              } else {
                this.$Indicator.close()
                this.$Toast({
                    message: '没有更多课程了',
                    position: 'middle',
                    duration: 1000
                })
                this.pagination = false
                this.showData = true
                this.falgNoData = true
              }
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      // 显示登录弹出层
      showDialogEvent(data) {
        this.showLoginModal = data;
      },
      // 收藏课程
      favorite(item) {
        if (!this.LOGIN_USER_OBJECT) {
          this.showLoginModal = 1
          return
        }
        if (item.isFollowed == 'N') {

          this.COURSES_ADD({
            courseId: item.courseId
          }).then( data => {
               if (data.data == '添加成功') {
                this.$set(item, 'isFollowed', 'Y')
                this.$Toast({
                    message: data.data,
                    position: 'middle',
                    duration: 1000
                })
              }
          }).catch(err => {
            console.log(err)
          })
        } else {
          this.COURSES_DELETE({
            courseId: item.courseId
          }).then(data => {
            if (data.data == '移除成功') {
              this.$Toast({
                  message: data.data,
                  position: 'middle',
                  duration: 1000
              })
              this.$set(item, 'isFollowed', 'N')
            }
          }).catch(err => {
            console.log(err)
          })
        }


        for (const key in this.$store.state.homeModule) {
          this.$store.state.homeModule[key] = []
        }
      },
      // 进入课程
      __enterCourse(info) {
        if (!this.LOGIN_USER_OBJECT) {
          this.showLoginModal = 1
          return;
        }
        if(info.liveStatus === 'LIVING'){
          this.$router.push({
                name: 'live',
                params: {
                    id: info.defaultLiveChannel,
                    courseId: info.courseId,
                }
            });
        }else{
          this.$Toast({
              message: '暂无直播',
              position: 'middle',
              duration: 500
          })
        }
      },
      // 进入VIP课程
      __enterVipCourse(item) {
        this.$router.push({
          name: 'vipDetail',
          params: {
            id: item.courseId
          }
        });
      },
      // 返回
      back() {
        this.$router.go(-1)
      },
      // 取消
      cancel() {
        this.$refs.searchWork.value = ''
        this.showList = true
      },
      // 查找课程
      searchWorks() {
        if (this.$refs.searchWork.value.trim().length > 0) {
          this.$Indicator.open({
              text: '加载中...',
              spinnerType: 'fading-circle'
          })
          this.showList = false;
          this.SEARCH_COURSES({
            title: this.$refs.searchWork.value.trim()
          }).then( data => {
            this.isSearch = true
            this.searchList = data.data.result
            if (data.data.result == '没有更多课程了') {
              this.searchList = []
              this.$Toast({
                  message: '找不到课程信息',
                  position: 'middle',
                  duration: 500
              })
            }
            this.$Indicator.close()
          }).catch(err => {
            console.log(err)
          })
        } else {
          this.showList = true
          this.isSearch = false
        }
      }
    },
    created() {
      // console.log(this)
    }
  }
</script>


